::-webkit-scrollbar {
	width: 3px;
	height: 3px;
}
::-webkit-scrollbar-track {
	background: linear-gradient(to bottom left, #3A7DD9, #4C4CB3, #5D38B7);
}
::-webkit-scrollbar-thumb {
	background: #3A7DD9;
	&:hover {
		background: #6253e8;
	}
}
*, html {
	user-select: none;
}
body {
	margin: 0;
	padding-top: 40px;
	padding-right: 0;
	background: linear-gradient(to bottom left, #3A7DD9, #4C4CB3, #5D38B7);
	color: #f5f5f5;
	font-family: 'Exo', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	scroll-behavior: smooth;
	scrollbar-color: #4C4CB3 #232135;
	scrollbar-width: thin;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}  