.hit-the-floor {
    animation: change_colors 6s infinite;
    color: transparent;
    font-size: 20vw;
    font-weight: bold;
    font-family: Helvetica;
    background-color: transparent;
    min-height: 50vh;
    margin: 3%;
    text-shadow: 0 0.005em 0 rgba(0, 0, 0, 0.2), 0 0.005em 0 currentColor, 0 0.01em 0 rgba(0, 0, 0, 0.25), 0 0.01em 0 currentColor, 0 0.015em 0 rgba(0, 0, 0, 0.3), 0 0.015em 0 currentColor, 0 0.02em 0 rgba(0, 0, 0, 0.35), 0 0.02em 0 currentColor, 0 0.025em 0 rgba(0, 0, 0, 0.4), 0 0.025em 0 currentColor, 0 0.03em 0.005em rgba(0, 0, 0, 0.1), 0 0 0.025em rgba(0, 0, 0, 0.1), 0 0.005em 0.015em rgba(0, 0, 0, 0.3), 0 0.015em 0.025em rgba(0, 0, 0, 0.2), 0 0.025em 0.05em rgba(0, 0, 0, 0.25), 0 0.05em 0.05em rgba(0, 0, 0, 0.2), 0 0.1em 0.1em rgba(0, 0, 0, 0.15);
}
.Error {
    display: flex;
    align-items: center;
    flex-direction: column;
        a {
            color: white;
        }
}
.textError {
    color: #05BECD;
    font-size: 36px;
    font-weight: 500;
    padding-bottom: 5%;
}
.return {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 100px;
}
@keyframes change_colors {
    0% {
        color: #5150ea;
    }
    12% {
        color: #ef4b7d;
    }
    25% {
        color: #fd4a82;
    }
    37% {
        color: rgba(248, 149, 134, 1);
    }
    50% {
        color: #fc7f63;
    }
    62% {
        color: #6665ed;
    }
    75% {
        color: #62ccfe;
    }
    87% {
        color: rgba(68, 146, 163, 1);
    }
    100% {
        color: #aa66f7;
    }
}
@media all and (max-width: 1000px) {
    .Error {
        width: 100%;
    }
    .textError {
        font-size: 18px;
        text-align: center;
    }
    .return {
    font-size: 14px;
    }
}  